import React, { useState, useEffect } from "react";
import { Box, Anchor } from "grommet";
import ContactList from "./ContactList";
import Chat from "../Chatroom";
const Component = ({ initialActiveId, senderRoleName, recipientRoleName, message }) => {
  
  
  const [activeChat, setActiveChat] = useState({
    senderRoleName,
    recipientRoleName
  });

  useEffect(() => {
    setActiveChat({
      senderRoleName,
      recipientRoleName
    })
  }, [senderRoleName, recipientRoleName])
  return (
    <Box height="100%" overflow="scroll">
      {!activeChat.senderRoleName && (
        <ContactList
          initialActiveId={initialActiveId}
          senderRoleName={senderRoleName}
          onClick={({ recipientRoleName }) => {
            setActiveChat({
              senderRoleName,
              recipientRoleName,
            });
          }}
        />
      )}
      {activeChat.senderRoleName && <Anchor onClick={() => setActiveChat({})}>Back</Anchor>}
      {activeChat.senderRoleName && <Chat {...activeChat} />}
    </Box>
  );
};

export default Component;
